export function setStorageCredit(credit) {
    const vals = {
        credit
    };
    return new Promise((resolve) => {
        chrome.storage.local.set(vals, () => {
            resolve();
        });
    });
}
export function setStoredUserInfo(email, family_name, given_name, picture) {
    const vals = {
        email,
        family_name,
        given_name,
        picture
    };
    return new Promise((resolve) => {
        chrome.storage.local.set(vals, () => {
            resolve();
        });
    });
}
export function setStoredForm(lang, ask) {
    const vals = {
        ask,
        lang,
    };
    return new Promise((resolve) => {
        chrome.storage.local.set({ "resultForm": vals }, () => {
            resolve();
        });
    });
}
export function setStoredDataResult(data) {
    const val = {
        data
    };
    return new Promise((resolve) => {
        chrome.storage.local.set({ "resultData": val }, () => {
            resolve();
        });
    });
}
export function getStoredCredit() {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get("credit", (result) => {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError);
            }
            else {
                resolve(result);
            }
        });
    });
}
export function getStoredUserInfo() {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get(null, (result) => {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError);
            }
            else {
                resolve(result);
            }
        });
    });
}
export function getStoredForm() {
    return new Promise((resolve, reject) => {
        chrome.storage.local.get("resultForm", (result) => {
            if (chrome.runtime.lastError) {
                reject(chrome.runtime.lastError);
            }
            else {
                // result.resultForm로부터 값을 추출
                resolve(result.resultForm);
            }
        });
    });
}
